.options
    width: 100%
    height: 100%
    position: absolute
    background: url("../../images/bg.jpg") center center / cover no-repeat
    z-index: 10
    transition: .6s all
    &__outer
        width: 510px
        height: 270px
        position: absolute
        top: calc(50% - 135px)
        left: calc(50% - 255px)
        overflow: hidden
        border: 10px solid darkgreen
    &__body, &__bodyTime, &__bodyDifficulty
        width: 500px
        height: 251px
        background: green
        display: flex
        flex-direction: column
        justify-content: space-between
    &__body
        padding: 40px
        position: relative
    &__bodyTime
        position: relative
        padding: 20px
    &__bodyDifficulty
        position: relative
        padding: 30px 0
    &__back, &__backDifficulty
        position: absolute
        font-size: 17px
        cursor: pointer
        left: 10px
        top: 10px
        user-select: none
    &__heading
        text-align: center
        font-size: 25px
        font-weight: 600
    &__question
        display: inline-block
        border: 3px solid black
        border-radius: 100%
        font-weight: 700
        width: 35px
        height: 35px
        font-size: 20px
        padding: 1px
        margin-left: 10px
        user-select: none
        cursor: pointer
    &__container
        display: flex
        justify-content: space-around
        margin-top: 20px
    &__choice
        cursor: pointer
        user-select: none
        text-align: center
        font-size: 20px
        transition: transform .4s, box-shadow .4s, background .3s
        background: #009900
        line-height: 45px
        height: 45px
        padding: 0 15px
        margin-top: 5px
        border-radius: 10px
        position: relative
        &:hover
            transform: translate(5px, -5px)
            box-shadow: -1px 1px 1px #006400, -2px 2px 1px #006400, -3px 3px 1px #006400, -4px 4px 1px #006400, -5px 5px 1px #006400
    &__helpMode
        background: #009900
        padding: 15px
        border-radius: 10px
        position: absolute
        top: 268px
        right: 177px
        width: 430px
        transition: all .4s
        z-index: 10
        &::after
            content: ""
            position: absolute
            top: calc( 50% - 25px )
            left: -24px
            border: 25px solid transparent
            border-left: none
            border-right-color: #009900
    &__helpTime
        background: #009900
        padding: 15px
        border-radius: 10px
        position: absolute
        top: 238px
        right: 155px
        width: 430px
        transition: all .4s
        z-index: 10
        &::after
            content: ""
            position: absolute
            top: calc( 50% - 25px )
            left: -24px
            border: 25px solid transparent
            border-left: none
            border-right-color: #009900

.difficulty
    width: 375px
    margin: 0 auto
    &__option
        width: 140px
        margin-top: 25px

.hover
    &-computer
        &:hover
            background: #a600ff
    &-sandbox
        &:hover
            background: #249dff
    &-beginner
        &:hover
            background: #249dff
    &-intermediate
        &:hover
            background: #d0c900
    &-advanced
        &:hover
            background: #d08b00
    &-master
        &:hover
            background: #a82700
    &-white
        &:hover
            background: #ffffff
    &-random
        &:hover
            background: linear-gradient(45deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 100%)
    &-black
        &:hover
            background: #000000
            color: white
    &-ten
        &:hover
            background: #c132ff
    &-five
        &:hover
            background: #32a6ff
    &-three
        &:hover
            background: #32ffb0